<template>
  <span class="star" :class="starType">
    <span v-for="starNum in starNums" :class="starNum" class="star-item"></span>
  </span>
</template>

<script>
  export default {
    props: {
      size: {
        type: Number
      },
      starcount: {
        type: Number
      }
    },
    data() {
      return {};
    },
    computed: {
      starType() {
        return 'star_' + this.size;
      },
      starNums() {
        let starnums = [];
        let num = Math.floor(this.starcount * 2) / 2;
        for (let i = 1; i <= num; i++) {
          starnums.push('on');
        }
        if (num % 1 !== 0) {
          starnums.push('half');
        }
        while (starnums.length < 5) {
          starnums.push('off');
        }
        return starnums;
      },
    },
  }
</script>

