            this.num = 0;
            break;
          }
          case 1: {
            this.newrating = this.rating.filter((val) => {
              return val.rateType == 0;
            });
            this.curIndex = 1;
            this.num = 1;
            break;
          }
          case 2: {
            this.newrating = this.rating.filter((val) => {
              return val.rateType == 1;
            });
            this.curIndex = 2;
            this.num = 2;
            break;
          }
        }
      },
      only() {
