<template>
  <div id="head" v-if="JSON.stringify(sellerTitle) != '{}'">
    <div class="headBgColor">
      <div class="headerBgImg"><img :src="sellerTitle.avatar" alt="" width="100%" height="135"></div>
      <div class="headPosition">
        <!--店铺图标-->
        <div class="shopImg">
          <img :src="sellerTitle.avatar" alt="" width="64px" height="64px"/>
        </div>
        <!--店铺描述-->
        <div class="content">
          <!--店铺名称-->
          <div class="shopTitle">
            <img
              src="@/components/header/brand@2x.png"
              alt=""
              width="30px"
            /><span>{{ sellerTitle.name }}</span>
          </div>
          <!--外卖信息-->
          <div class="middleTitle">
            {{ sellerTitle.description }}/{{ sellerTitle.deliveryTime }}分钟送达
          </div>
          <!--满减信息-->
          <div class="bottomTitle">
            <img
              src="@/components/header/decrease_1@2x.png"
              alt=""
              width="12px"
            /><span>{{
            sellerTitle.supports[0].description
            }}</span>
          </div>
        </div>
      </div>
      <!--优惠券-->
      <div class="coupon" @click="showDetail">{{ sellerTitle.supports.length }}个 ></div>
      <!--公告-->
      <div class="notice">
        <!--公告图标-->
        <span
        ><img src="@/components/header/bulletin@2x.png" alt="" width="24px"
        /></span>
        <!--公告内容-->
        <span class="contextOrder" @click="showDetail">
          <span>{{ sellerTitle.bulletin }}</span>
        </span>
        <span class="todetail">></span>
      </div>
      <transition name="show">
        <div v-show="detailShow" class="sellerdetail">
          <div class="detailcontent">
            <div class="detailtitle">{{sellerTitle.name}}</div>
            <div>
              <star :size="48" :starcount="sellerTitle.score"></star>
            </div>
            <div class="dtitles"><span class="detailsplit"></span><span class="dtitle">优惠信息</span><span
              class="detailsplit"></span></div>
            <div class="detailinfo">
              <ul>
                <li v-for="(item,index) in sellerTitle.supports" :key="index">
                  <span class="icon" :class="classMap[sellerTitle.supports[index].type]"></span>
                  <span>{{sellerTitle.supports[index].description}}</span>
                </li>
              </ul>
            </div>
            <div class="dtitles"><span class="detailsplit"></span><span class="dtitle">商家公告</span><span
              class="detailsplit"></span></div>
            <div class="detailnotice">
              <div class="noticecontent">
                {{sellerTitle.bulletin}}
              </div>
            </div>
            <div class="close" @click="closeDetail">
              ×
            </div>
          </div>
        </div>
      </transition>

    </div>
  </div>

</template>
<script>
  //@ is an alias to /src
  import star from '@/components/star/star'

  export default {
    name: "",
    components: {
      star
    },
    props: {
      sellerTitle: {
        type: Object,
      },
    },
    data(){
      return{
        detailShow: false
      }
    },
    methods:{
      showDetail(){
        this.detailShow = true;
      },
      closeDetail(){
        this.detailShow = false;
      }
    },
    created() {
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
    }
  };
</script>
<style lang="stylus">
  #head
    width: 100%
    height: 135px
    background-size: 100% 100%
    background-repeat: no-repeat

    .headerBgImg
      width: 100%
      position: absolute
      z-index: -1000
      filter: blur(7px)

    .headBgColor
      background-color: rgba(80, 80, 80, 0.7)
      width: 100%
      height: 100%
      box-sizing: border-box

    .headPosition
      padding: 25px 0 0 25px

    .shopImg
      display: inline-block

    .content
      display: inline-block
      font-size: 16px
      color: white
      vertical-align: top
      margin-left: 15px

    .shopTitle
      height: 20px
      line-height: 20px
      display: flex
      align-items: center
      font-weight: bolder

    .shopTitle span
      margin-left: 5px

    .middleTitle
      font-size: 12px
      margin: 7px auto

    .bottomTitle
      font-size: 12px
      display: flex
      align-items: center

    .bottomTitle span
      margin-left: 5px

    .coupon
      width: 50px
      height: 24px
      border-radius: 12px
      background-color: rgba(60, 60, 60, 0.7)
      position: absolute
      top: 10%
      left: 85%
      display: flex
      justify-content: center
      align-items: center
      color: white
      font-size: 12px

    .notice
      width: 100%
      height: 25px
      line-height: 25px
      background-color: rgba(60, 60, 60, 0.7)
      position: absolute
      top: 110px
      display: flex
      align-items: center
      color: white

    .notice img
      margin-left: 5px

    .contextOrder
      width: 90%
      font-size: 12px
      overflow: hidden
      text-overflow: ellipsis
      white-space: nowrap
      box-sizing: border-box
      & span
        margin-left: 5px
      & .todetail
        margin-left: -8px
    .sellerdetail
      position: fixed
      top: 0
      left: 0
      width: 100%
      height: 100%
      z-index: 1000
      background: rgba(7, 17, 27, 0.8)
      transition: all 0.5s
      &.show-enter, &.show-leave-active
        opacity: 0
        background: rgba(7,17,27,0)
      .detailcontent
        color white
        margin 60px 0
        display flex
        flex-direction column
        align-items center
        .detailtitle
          text-align center
          margin-bottom 20px
        .dtitles
          padding 20px 0
          display flex
          width 80%
          .detailsplit
            flex 1
            border-bottom 1px solid #828282
            margin-bottom 8px
          .dtitle
            font-size 14px
            font-weight bolder
            margin 0 10px
        .detailinfo
          width 80%
          & li
            font-size 12px
            padding 0 12px
            margin-bottom 12px
        .detailnotice
          width 80%
          margin 0 auto
          .noticecontent
            font-size 12px
            padding 0 12px
            line-height 24px
        .close
          position absolute
          bottom 25px
          font-size 45px
        bg-image($url)
          background-image url($url + "@2x.png")
        .icon
          display: inline-block
          width: 16px
          height: 16px
          margin-right: 16px
          vertical-align: top
          background-size: 16px 16px
          backgroun-repeat: no-repeat
          &.decrease
            bg-image('decrease_2')
          &.discount
            bg-image('discount_2')
          &.guarantee
            bg-image('guarantee_2')
          &.invoice
            bg-image('invoice_2')
          &.special
            bg-image('special_2')

</style>
