    watch: {
      selectFoods: function () {
      }
    },
    computed: {
      selectFoods() {
        let foods = [];
        this.good.forEach(good => {
          good.foods.forEach(food => {
            if (food.count) {
              foods.push(food);
            }
          })
        });
        this.$store.commit({
          type: 'changeFoods',
          foodList: foods
        });
      },
    },
    methods: {
      listHeight() {
        var _this = this;
        //每个list的高度，放到arrLength的数组里
        var foodList = document.getElementsByClassName("foodlist");
        for (let i = 0; i < foodList.length; i++) {
          _this.arrLength.push(foodList[i].offsetHeight);
        }
        //前i项的累加高度，放到sumlength数组里
        this.sumlength[0] = 0;
        for (let i = 1; i <= this.arrLength.length; i++) {
          this.sumlength.push(this.arrLength[i - 1] + this.sumlength[i - 1]);
        }
        //判断当前list在哪个区间，对应左边的选项
        document.querySelector(".sectionList").addEventListener("scroll", function () {
          var scrollTop = document.querySelectorAll(".sectionList")[0].scrollTop;
          for (let i = 1; i <= _this.sumlength.length; i++) {
            if (
              scrollTop >= _this.sumlength[i - 1] &&
              scrollTop < _this.sumlength[i]
            ) {
              _this.cur = i - 1;
              break;
            }
          }
        });
      },
      sumLength(index) {
        //对应前index项的累加
        var l = 0;
        for (let i = 0; i < index; i++) {
          l += this.arrLength[i];
        }
        return l;
      },
      cli(index) {
        this.cur = index;
        document.querySelectorAll(".sectionList")[0].scrollTop = this.sumLength(index);
      },
      selectFood(food) {
        this.selectedFood = food;
        this.$refs.food.show();
      },
    },
    created() {
      /*this指向vue的实例对象*/
      this.imgs = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
      /*this.$http.get('/api/goods').then((data) => {
        this.good = data.data.data;

      }).catch((err) => {
        console.log(err);
      });*/
    },
    updated() {
      this.$nextTick(() => {
        this.listHeight();
      })
    },
    components: {
      //2.在父组件这里注册子组件
      detail,
      cartcontrol,
      shopcart,
    },
  }
