<template>
  <transition name="move">
    <div v-show="foodShow"  class="food">
      <div class="content">
        <div class="img-content">
          <img class="img" :src="food.image">
          <div class="back" @click="back">
            <i class="icon-arrow_lift"></i>
          </div>
        </div>
        <div class="food-detail">
          <div class="detail-name">{{food.name}}</div>
          <div class="detail-info">
            <span class="sell-count">月售{{food.sellCount}}份</span>
            <span class="rating">好评率{{food.sellCount}}</span>
          </div>
          <div class="price">
            ￥
            <span class="now">{{food.price}}</span>
            <span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
          </div>
          <transition name="buyFade">
            <div class="buy" v-show="!food.count || food.count===0" @click.stop.prevent="addFirst">加入购物车</div>
          </transition>
          <div class="cart-control">
            <cartcontrol :food="food"></cartcontrol>
          </div>
        </div>
        <div class="split" v-show="food.info"></div>
        <div class="food-info" v-show="food.info">
          <div class="info-title">商品信息</div>
          <div class="text">{{food.info}}</div>
        </div>
        <div class="split"></div>
        <div class="rating">
          <div class="title">商品评价</div>
          <div class="rating-content">
            <ul v-show="food.ratings && food.ratings.length">
              <li class="rating-item"  v-for="rating in food.ratings">
                <div class="user">
                  <span class="name">{{rating.username}}</span>
                  <img class="avatar" width="12" height="12" :src="rating.avatar">
                </div>
                <div class="time">{{rating.rateTime | format}}</div>
                <div class="text">
                  <span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>{{rating.text}}
                </div>
              </li>
            </ul>
            <div v-show="!food.ratings || food.ratings.length===0" class="no-rating">暂无评价</div>
          </div>
        </div>
      </div>
    </div>
  </transition>

</template>

<script>
import {formatDate} from '@/common/js/formatDate'
import cartcontrol from '@/components/cartcontrol/cartcontrol'
export default {
  data() {
    return {
      foodShow: false,
    }
  },
  props: {
    food: {
      type: Object
    }
  },
  methods: {
    show() {
      this.foodShow = true;
    },
    back() {
      this.foodShow = false;
    },
    addFirst() {
      if (!this.food.count) {
        this.$set(this.food, 'count', 1);
      } else {
        this.food.count++;
      }
      this.$store.commit({
        type: 'initBallEle',
        el: event.target
      });
      this.$root.$emit('add_cart');
    },
  },
  filters: {
    format(time) {
      let date = new Date(time);
      return formatDate(date,'yyyy-MM-dd hh:mm');
    }
  },
  components:{
    cartcontrol
  }
};
</script>

